import iconClose from '../../imgs/icon-close.svg'
import './styles.css'

export type CloseReason = 'button' | 'overlay' | 'escape'

export interface ModalOptions {
  content: () => HTMLElement
  onClose?: (reason: CloseReason) => void
}

// Module-level singleton tracking
const modalState = {
  active: null as ModalWidget | null
}

export class ModalWidget {
  private container: HTMLElement
  private options: ModalOptions
  private modalElement: HTMLElement | null = null
  private previousActiveElement: HTMLElement | null = null
  private boundHandleKeyDown: (e: KeyboardEvent) => void

  constructor(container: HTMLElement, options: ModalOptions) {
    this.container = container
    this.options = options
    this.boundHandleKeyDown = this.handleKeyDown.bind(this)
  }

  render(): void {
    const template = `
      <div class="miso-modal" role="dialog" aria-modal="true">
        <div class="miso-modal__overlay"></div>
        <div class="miso-modal__dialog">
          <button class="miso-modal__close-btn" aria-label="Close modal">
            <img src="${iconClose}" alt="Close" />
          </button>
          <div class="miso-modal__content"></div>
        </div>
      </div>
    `

    this.container.innerHTML = template
    this.modalElement = this.container.querySelector('.miso-modal')

    // Render content from render function
    const contentContainer = this.modalElement?.querySelector('.miso-modal__content')
    if (contentContainer && this.options.content) {
      const contentElement = this.options.content()
      contentContainer.appendChild(contentElement)
    }

    this.attachEventListeners()
  }

  private attachEventListeners(): void {
    if (!this.modalElement) return

    // Close button click
    const closeBtn = this.modalElement.querySelector('.miso-modal__close-btn')
    closeBtn?.addEventListener('click', () => this.closeModal('button'))

    // Overlay click
    const overlay = this.modalElement.querySelector('.miso-modal__overlay')
    overlay?.addEventListener('click', () => this.closeModal('overlay'))

    // Prevent dialog clicks from closing
    const dialog = this.modalElement.querySelector('.miso-modal__dialog')
    dialog?.addEventListener('click', (e) => e.stopPropagation())
  }

  private handleKeyDown(e: KeyboardEvent): void {
    if (e.key === 'Escape') {
      e.preventDefault()
      this.closeModal('escape')
    }

    // Focus trap
    if (e.key === 'Tab' && this.modalElement) {
      const focusableElements = this.modalElement.querySelectorAll<HTMLElement>(
        'button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])'
      )
      const firstElement = focusableElements[0]
      const lastElement = focusableElements[focusableElements.length - 1]

      if (e.shiftKey && document.activeElement === firstElement) {
        e.preventDefault()
        lastElement?.focus()
      } else if (!e.shiftKey && document.activeElement === lastElement) {
        e.preventDefault()
        firstElement?.focus()
      }
    }
  }

  openModal(): void {
    // Close any existing modal first (singleton)
    if (modalState.active && modalState.active !== this) {
      modalState.active.closeModal('button')
    }

    if (!this.modalElement) return

    // Store current focus for restoration
    this.previousActiveElement = document.activeElement as HTMLElement

    // Lock body scroll
    document.body.style.overflow = 'hidden'

    // Add keyboard listener
    document.addEventListener('keydown', this.boundHandleKeyDown)

    // Force a reflow to ensure initial state is rendered before adding open class
    // This allows the CSS transition to animate from translateY(100%) to translateY(0)
    void this.modalElement.offsetHeight

    // Show modal with animation
    this.modalElement.classList.add('miso-modal--open')

    // Set as active modal
    modalState.active = this

    // Focus first focusable element
    const firstFocusable = this.modalElement.querySelector<HTMLElement>(
      'button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])'
    )
    firstFocusable?.focus()
  }

  closeModal(reason: CloseReason = 'button'): void {
    if (!this.modalElement) return

    // Remove keyboard listener
    document.removeEventListener('keydown', this.boundHandleKeyDown)

    // Start close animation
    this.modalElement.classList.remove('miso-modal--open')

    // Wait for animation to complete
    setTimeout(() => {
      // Restore body scroll
      document.body.style.overflow = ''

      // Clear active modal
      if (modalState.active === this) {
        modalState.active = null
      }

      // Restore focus
      this.previousActiveElement?.focus()

      // Call onClose callback
      this.options.onClose?.(reason)
    }, 300) // Match CSS transition duration
  }

  destroy(): void {
    // Close if open
    if (modalState.active === this) {
      document.removeEventListener('keydown', this.boundHandleKeyDown)
      document.body.style.overflow = ''
      modalState.active = null
    }

    // Remove from DOM
    if (this.modalElement) {
      this.modalElement.remove()
      this.modalElement = null
    }

    this.container.innerHTML = ''
  }
}
