import { MisoWidgetInstance } from './core/instance'
import type { WidgetInstance, MisoForMyRSDK, UserInfo } from './types/widget'
import './styles/variables.css'

export type { WidgetConfig, WidgetInstance, UserInfo, PlanListingCallbacks, PlanDetailCallbacks, PlanDetailOptions, MisoForMyRSDK } from './types/widget'
export type { Plan, Recipe, Message, Messages } from './types/api'

// Singleton instance
let instance: MisoWidgetInstance | null = null

function getOrCreateInstance(): MisoWidgetInstance {
  if (!instance) {
    instance = new MisoWidgetInstance()
  }
  return instance
}

/**
 * SDK namespace for the widget.
 * Provides methods to set auth token, manage user info, and render widgets.
 */
const misoForMyRSDK: WidgetInstance = {
  setAuthToken(token: string): WidgetInstance {
    return getOrCreateInstance().setAuthToken(token)
  },

  clearAuthToken(): WidgetInstance {
    return getOrCreateInstance().clearAuthToken()
  },

  setUserInfo(info: UserInfo): WidgetInstance {
    return getOrCreateInstance().setUserInfo(info)
  },

  clearUserInfo(): WidgetInstance {
    return getOrCreateInstance().clearUserInfo()
  },

  on(event: string, callback: any): void {
    getOrCreateInstance().on(event, callback)
  },

  off(event: string, callback: any): void {
    getOrCreateInstance().off(event, callback)
  },

  renderPlanListingWidget(container: HTMLElement, callbacks?: any): void {
    getOrCreateInstance().renderPlanListingWidget(container, callbacks)
  },

  destroyPlanListingWidget(): void {
    getOrCreateInstance().destroyPlanListingWidget()
  },

  renderPlanDetailWidget(container: HTMLElement, planId: string | null, options?: any): void {
    getOrCreateInstance().renderPlanDetailWidget(container, planId, options)
  },

  destroyPlanDetailWidget(): void {
    getOrCreateInstance().destroyPlanDetailWidget()
  },

  getPlans(): Promise<any[]> {
    return getOrCreateInstance().getPlans()
  },

  refreshPlans(): Promise<void> {
    return getOrCreateInstance().refreshPlans()
  },

  get planListingWidget(): any | null {
    return getOrCreateInstance().planListingWidget
  },

  get planDetailWidget(): any | null {
    return getOrCreateInstance().planDetailWidget
  },
}

// Export as global for IIFE bundle
if (typeof window !== 'undefined') {
  (window as any).misoForMyR = misoForMyRSDK
}

// Export for ESM imports
export default misoForMyRSDK
