import type { Plan } from '../../types/api'

/**
 * Main state interface for the Plan Detail widget.
 * All UI-related state is centralized here.
 */
export type CreationPhase = 'idle' | 'searching' | 'validating' | 'building'
export type PlanMode = 'view' | 'edit'

export interface PlanEditorState {
  plan: Plan | null
  planId: string | null
  initialMessage: string | undefined
  mode: PlanMode
  isLoading: boolean
  isUpdating: boolean
  isThinking: boolean
  planNotFound: boolean
  errorMessage: string
  favoriteRecipes: string[]
  suggestions: string[]
  creationPhase: CreationPhase
  creationMessage: string
  shouldPreserveScroll: boolean
}

/**
 * Creates the initial state for the Plan Detail widget.
 */
export function createInitialState(planId: string | null, initialMessage?: string, mode?: PlanMode): PlanEditorState {
  // If initialMessage is provided, always start in edit mode
  const resolvedMode = initialMessage ? 'edit' : (mode || 'view')
  
  return {
    plan: null,
    planId,
    initialMessage,
    mode: resolvedMode,
    isLoading: false,
    isUpdating: false,
    isThinking: false,
    planNotFound: false,
    errorMessage: '',
    favoriteRecipes: [],
    suggestions: [],
    creationPhase: 'idle',
    creationMessage: '',
    shouldPreserveScroll: false,
  }
}

/**
 * State key types for partial update handlers
 */
export type StateKey = keyof PlanEditorState

/**
 * Handler function type for partial updates
 */
export type StateHandler<T> = (value: T, state: PlanEditorState) => void

/**
 * Partial handler map - only keys with handlers will trigger partial updates
 */
export type StateHandlers = {
  [K in StateKey]?: StateHandler<PlanEditorState[K]>
}

/**
 * State manager with partial update support.
 * When a state key has a registered handler, only that handler is called.
 * When no handler exists for any changed key, the fallback (full re-render) is called.
 */
export interface StateManager {
  get: () => PlanEditorState
  set: (partial: Partial<PlanEditorState>) => void
  refresh: () => void
}

/**
 * Creates a state manager with partial update support.
 * 
 * @param initialState - The initial state object
 * @param handlers - Map of state keys to their partial update handlers
 * @param fallback - Full re-render function called when no specific handler exists
 * @returns StateManager object with get, set, and refresh methods
 * 
 * @example
 * ```ts
 * const state = createState(
 *   createInitialState(null),
 *   {
 *     savedRecipes: (recipes) => updateSaveButtons(),
 *     errorMessage: (msg) => updateErrorDisplay(),
 *   },
 *   () => renderContent()
 * )
 * 
 * // Partial update - only calls updateSaveButtons()
 * state.set({ savedRecipes: ['123'] })
 * 
 * // Full re-render - plan has no handler
 * state.set({ plan: newPlan })
 * ```
 */
export function createState(
  initialState: PlanEditorState,
  handlers: StateHandlers,
  fallback: () => void
): StateManager {
  let state = { ...initialState }

  return {
    get: () => state,

    set: (partial: Partial<PlanEditorState>) => {
      const changedKeys = Object.keys(partial) as StateKey[]
      
      // Update state
      state = { ...state, ...partial }

      // Track if any handler was called
      let handlerCalled = false

      // Call specific handlers for changed keys
      for (const key of changedKeys) {
        const handler = handlers[key]
        if (handler) {
          // Type assertion needed due to TypeScript limitations with mapped types
          ;(handler as StateHandler<PlanEditorState[typeof key]>)(state[key], state)
          handlerCalled = true
        }
      }

      // If no specific handler was called, do full re-render
      if (!handlerCalled) {
        fallback()
      }
    },

    // Force full re-render regardless of handlers
    refresh: () => fallback(),
  }
}
