import { unified } from 'unified'
import remarkParse from 'remark-parse'
import remarkGfm from 'remark-gfm'
import remarkRehype from 'remark-rehype'
import rehypeStringify from 'rehype-stringify'

/**
 * Parses markdown content and converts it to HTML
 * 
 * @param markdown - The markdown string to parse
 * @returns HTML string
 */
export async function parseMarkdown(markdown: string): Promise<string> {
  const result = await unified()
    .use(remarkParse)
    .use(remarkGfm)
    .use(remarkRehype)
    .use(rehypeStringify)
    .process(markdown)
  
  return String(result)
}

/**
 * Synchronous version of parseMarkdown for use in renderers
 * Uses processSync for immediate rendering
 * 
 * @param markdown - The markdown string to parse
 * @returns HTML string
 */
export function parseMarkdownSync(markdown: string): string {
  const result = unified()
    .use(remarkParse)
    .use(remarkGfm)
    .use(remarkRehype)
    .use(rehypeStringify)
    .processSync(markdown)
  
  return String(result)
}
